#navbar {
    display: flex;
    justify-content: center;
    color: #FFFBF9;
    margin: 0 23%;
}

.navbarComponent {
    padding: 0 32px;
    background-color: transparent;
    color: white;
    font-size: 22px;
    font-family: "Lucida Console";
    border: transparent;
    cursor: pointer;
}

.navbarComponent:hover {
    font-weight: bold;
}

.glowbarOn {
    height: 4px;
    width: 90%;
    margin-top: 0.4em;
    background-color: #2DE3FF;
    box-shadow: 0px 0px 10px #2DE3FF;
}

.glowbarOff {
    height: 4px;
    margin-top: 0.4em;
    background-color: transparent;
}

.navbarComponentWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}